import ApiService from '@/services/ApiService.js'
export const namespaced = true

export const state = {
  books: [],
  booksInUse: [],
  book: {}
}

export const mutations = {
  SET_BOOKS(state, books) {
    state.books = books
  },
  SET_BOOK(state, book) {
    state.book = book
  },
  SET_BOOKS_IN_USE(state, booksInUse) {
    state.booksInUse = booksInUse
  }
}

export const actions = {
  async fetchBooksInUse({ commit }) {
    const request = {
      method: 'get',
      url: '/book_in_use/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOKS_IN_USE', response.data)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async fetchBooks({ commit }) {
    const request = {
      method: 'get',
      url: '/books/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOKS', response.data)
      return response.data
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async fetchBook({ commit }, id) {
    const request = {
      method: 'get',
      url: '/books/' + id
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOK', response.data)
    } catch (error) {
      console.log(error)
      throw error
    }
  },

  async searchBooks({ commit }, search) {
    const request = {
      method: 'get',
      url: '/books/?search=' + search
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_BOOKS', response.data)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  }
}
export const getters = {}
