export const mutations = {
  SET_ORDERS(state, orders) {
    state.orders = orders
  },
  SET_ORDER(state, order) {
    state.order = order
  },
  ORDER_BOOK(state, book) {
    state.books.push(book)
  }
}

export const actions = {
  async fetchOrders({ commit }) {
    const request = {
      method: 'get',
      url: '/orders/'
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_ORDERS', response.data)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async fetchOrder({ commit }, id) {
    const request = {
      method: 'get',
      url: '/orders/' + id
    }
    try {
      const response = await ApiService.customRequest(request)
      commit('SET_ORDER', response.data)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async orderBook(context, order) {
    const request = {
      method: 'post',
      url: '/orders/create/',
      data: order
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      console.log(error)
      throw error
    }
  },

  async updateOrder(context, { id, order }) {
    console.log(order)
    const request = {
      method: 'patch',
      url: '/orders/' + id,
      data: order
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  }
}

