import TokenService from '@/services/TokenService'
import ApiService from '@/services/ApiService'
import jwtDecode from 'jwt-decode'

export const namespaced = true

export const state = {
  user: null,
  accessToken: TokenService.getToken('access'),
  refreshToken: TokenService.getToken('refresh'),
  userId: null
}
export const mutations = {
  SET_AUTH_USER(state, user) {
    state.user = user
  },
  SET_USER_ID(state, userId) {
    state.userId = userId
  },
  LOGOUT() {
    localStorage.removeItem('access')
    localStorage.removeItem('refresh')
    location.reload()
  }
}
export const actions = {
  async login({ commit }, credentials) {
    const request = {
      method: 'post',
      url: '/token/',
      data: credentials
    }
    try {
      const response = await ApiService.customRequest(request)
      const userId = jwtDecode(response.data.access).user_id
      ApiService.setHeader('Authorization', `Bearer ${response.data.access}`)
      TokenService.saveToken('access', response.data.access)
      TokenService.saveToken('refresh', response.data.refresh)
      commit('SET_AUTH_USER', response.data)
      commit('SET_USER_ID', userId)
    } catch (error) {
      console.log(error.message)
      throw error
    }
    // return ApiService.customRequest(request)
    //   .then(response => {
    //     const userId = jwtDecode(response.data.access).user_id
    //     ApiService.setHeader('Authorization', `Bearer ${response.data.access}`)
    //     TokenService.saveToken('access', response.data.access)
    //     TokenService.saveToken('refresh', response.data.refresh)
    //     commit('SET_AUTH_USER', response.data)
    //     commit('SET_USER_ID', userId)
    //   })
    //   .catch(error => {
    //     throw error
    //   })
  },

  async register(context, credentials) {
    const request = {
      method: 'post',
      url: 'https://autolib.tdtu.uz/auth/users/',
      data: credentials
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async emailConfirm(context, credentials) {
    const request = {
      method: 'post',
      url: 'https://autolib.tdtu.uz/auth/users/activation/',
      data: credentials
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      throw new error()
    }
  },

  async refreshToken({ commit }, refreshToken) {
    const request = {
      method: 'post',
      url: '/token/refresh/',
      data: refreshToken
    }
    try {
      const response = await ApiService.customRequest(request)
      const userId = jwtDecode(response.data.access).user_id
      ApiService.setHeader('Authorization', `Bearer ${response.data.access}`)
      TokenService.saveToken('access', response.data.access)
      commit('SET_USER_ID', userId)
    } catch (error) {
      await this.refreshToken({ commit }, refreshToken)
      throw error
    }
  },

  async resetPassword(context, email) {
    const request = {
      method: 'post',
      url: 'https://autolib.tdtu.uz/auth/users/reset_password/',
      data: email
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  async resetPasswordConfirm(context, credentials) {
    const request = {
      method: 'post',
      url: 'https://autolib.tdtu.uz/auth/users/reset_password_confirm/',
      data: credentials
    }
    try {
      await ApiService.customRequest(request)
    } catch (error) {
      console.log(error.message)
      throw error
    }
  },

  logout({ commit }) {
    commit('LOGOUT')
  }
}
export const getters = {}
